//
// Typography
// --------------------------------------------------

// Font Smoothing
// -------------------------

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0.01em;
}


// Headings
// -------------------------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: @headings-font-family;
  font-weight: @headings-font-weight;
  line-height: @headings-line-height;
  color: @headings-color;
  margin-top: 0;
  margin-bottom: 0;
  text-transform: none;

  small,
  .small {
    font-weight: 300; //normal;
    line-height: 1;
    color: @headings-small-color;
  }
}

h1, .h1 {
  font-size: @font-size-h2;
  line-height: (@padding-base-vertical * 5);
}
h2, .h2 {
  font-size: @font-size-h3;
  line-height: (@padding-base-vertical * 4);
}
h3, .h3 { font-size: @font-size-h4; }
h4, .h4 { font-size: @font-size-h5; }
h5, .h5 { font-size: @font-size-h5; }
h6, .h6 { font-size: @font-size-h6; }

h1, .h1,
h2, .h2,
h3, .h3 {
  margin-bottom: @padding-base-vertical;
}

h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  line-height: (@padding-base-vertical * 3);
}

h4, .h4,
h5, .h5,
h6, .h6 {
  margin-bottom: (@padding-base-vertical / 2);
}

@media (min-width: @screen-md-min) {
  h1, .h1 {
    font-size: @font-size-h1;
    line-height: (@padding-base-vertical * 6);
  }
  h2, .h2 {
    font-size: @font-size-h2;
    line-height: (@padding-base-vertical * 5);
  }
  h3, .h3 {
    font-size: @font-size-h3;
    line-height: (@padding-base-vertical * 4);
  }
  h4, .h4 { font-size: @font-size-h4; }
  h5, .h5 { font-size: @font-size-h5; }
  h6, .h6 { font-size: @font-size-h6; }

  h1, h2, h3, h4, h5, h6,
  .h1, .h2, .h3, .h4, .h5, .h6 {
    margin-bottom: @padding-base-vertical;
 }
}


h1, .h1,
h2, .h2 {
  font-weight: 800;

  small,
  .small {
    font-size: 65%;
  }
}

h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  small,
  .small {
    font-size: 75%;
  }
}

h6, .h6 {
  color: @brand-grey-plus-20;
}

// Body text
// -------------------------

p {
  margin: 0 0 @line-height-computed;
}

.lead {
  margin-bottom: @line-height-computed;
  font-size: 18px;
  font-weight: 300;
  line-height: @line-height-large;

  @media (min-width: @screen-md-min) {
    font-size: 22px;
  }
}


// Emphasis & misc
// -------------------------

// Ex: (12px small font / 14px base font) * 100% = about 85%
small,
.small {
  font-size: @font-size-small;
  line-height: @line-height-small;
}

mark,
.mark {
  background-color: @state-warning-bg;
  padding: .2em;
}

b,
strong {
  color: @text-bold-color;
  font-weight: 600;
}

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-justify        { text-align: justify; }
.text-nowrap         { white-space: nowrap; }

// Transformation
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }

// Contextual colors
.text-muted {
  color: @text-muted;
}
.text-primary {
  .text-emphasis-variant(@brand-primary);
}
.text-success {
  .text-emphasis-variant(@state-success-text);
}
.text-info {
  .text-emphasis-variant(@state-info-text);
}
.text-warning {
  .text-emphasis-variant(@state-warning-text);
}
.text-danger {
  .text-emphasis-variant(@state-danger-text);
}
.text-inverse {
  .text-emphasis-variant(@brand-white);
}

.text-ellipsis,
.text-ellipses {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-primary {
  // Given the contrast here, this is the only class to have its color inverted
  // automatically.
  color: #fff;
  .bg-variant(@brand-primary);
}
.bg-success {
  .bg-variant(@state-success-bg);
}
.bg-info {
  //.bg-variant(@state-info-bg);
  color: #fff;
   #gradient > .vertical(@brand-light-blue, #36c9ff);
}
.bg-warning {
  .bg-variant(@state-warning-bg);
}
.bg-danger {
  .bg-variant(@state-danger-bg);
}


// Page header
// -------------------------

.page-header {
  padding-bottom: (@padding-base-vertical * 2);
  margin: 0 0  (@padding-base-vertical * 4);
  border-bottom: 1px solid @page-header-border-color;

  &:not(:first-child) {
      margin-top: (@padding-base-vertical * 4);
  }
}


// Lists
// -------------------------

// Unordered and Ordered lists
ul,
ol {
  padding-left: 38px;
  margin-top: 0;
  margin-bottom: @line-height-computed;
  ul,
  ol {
    margin-bottom: 0;
  }
}

// List options

// Unstyled keeps list items block level, just removes default browser padding and list-style
.list-unstyled {
  padding-left: 0;
  list-style: none;
}

// Inline turns list items into inline-block
.list-inline {
  .list-unstyled();
  margin-left: -4px;

  > li {
    display: inline-block;
    padding-left: 4px;
    padding-right: 8px;
    border-right: 1px solid @brand-grey-plus-10;

    &:last-child {
        border-right: 0;
    }
  }
}

// Description Lists
dl {
  margin-top: 0; // Remove browser default
  margin-bottom: 0;
}
dt,
dd {
  line-height: @line-height-computed;
}
dt {
  font-size: @font-size-small;
  color: @brand-grey-plus-10;
}
dd {
  color: @brand-blue-plus-20;
  margin-left: 0; // Undo browser default
  margin-bottom: @padding-container-collapse-xs;
}

// Horizontal description lists
//
// Defaults to being stacked without any of the below styles applied, until the
// grid breakpoint is reached (default of ~768px).

.dl-horizontal {
  dd {
    &:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present
  }

  @media (min-width: @screen-sm-min) {
    dt {
      float: left;
      width: (@dl-horizontal-offset - 24);
      clear: left;
      text-align: left;
      .text-overflow();
    }
    dd {
      margin-left: @dl-horizontal-offset;
      margin-bottom: (@padding-base-vertical * 2);
    }
  }
}
